import React from "react";
import FeaturedCategories from "./FeaturedCategories";
import featuredData from "./featuredData";
import brandsData from "./brandsData";
import Brand from "./Brand";
import Slider from "./Slider"

function MainContent() {

  const featuredCategories = featuredData.map(category => (
    <FeaturedCategories key={category.id} category={category} />
  ));

  const brands = brandsData.map(brand => (
    <Brand key={brand.id} brand={brand} />
  ));

  return (
    <section className="main-content">
      <Slider />
      <div>
        <h1 className="featured-categories-title">Featured Categories</h1>
        <div className="featured-categories">{featuredCategories}</div>
      </div>
      <div className="all-brands">
        <h1 className="our-brands-title">Our Brands</h1>
        <div className="our-brands">{brands}</div>
      </div>
    </section>
  );
}

export default MainContent;
