import weddingSlider from "./images/wedding-banner.jpg";
import springSlider from "./images/spring-banner.jpg";
import summerSlider from "./images/summer-banner.jpg";
import fallSlider from "./images/fall-banner.jpg";
import winterSlider from "./images/winter-banner.jpg";
import restaurantSlider from "./images/restaurant-banner.jpg";

const seasonSlider = () => {
  switch (new Date().getMonth()) {
    case 0:
      return winterSlider;
    case 1:
      return winterSlider;
    case 2:
      return springSlider;
    case 3:
      return springSlider;
    case 4:
      return springSlider;
    case 5:
      return summerSlider;
    case 6:
      return summerSlider;
    case 7:
      return summerSlider;
    case 8:
      return fallSlider;
    case 9:
      return fallSlider;
    case 10:
      return winterSlider;
    case 11:
      return winterSlider;
    default:
      return springSlider;
  }
};

const seasonTitle = () => {
  switch (new Date().getMonth()) {
    case 0:
      return "Winter";
    case 1:
      return "Winter";
    case 2:
      return "Spring";
    case 3:
      return "Spring";
    case 4:
      return "Spring";
    case 5:
      return "Summer";
    case 6:
      return "Summer";
    case 7:
      return "Summer";
    case 8:
      return "Fall";
    case 9:
      return "Fall";
    case 10:
      return "Winter";
    case 11:
      return "Winter";
    default:
      return "Spring"
  }
};

const slideData = [
  {
    id: 101,
    url: weddingSlider,
    title: "Wedding Napkins"
  },
  {
    id: 102,
    url: seasonSlider(),
    title: seasonTitle() + " Styles"
  },
  {
    id: 103,
    url: restaurantSlider,
    title: "Restaurant Napkins"
  }
];

export default slideData;
