import React from "react";

function MobileCategories(props) {
  const subCategories = props.category.active
    ? props.category.subCategories.map((subCategory, index) => (
        <li className="mobile-subcategories" key={index}>{subCategory}</li>
      ))
    : null;

  return (
    <ul>
      <li onClick={event => props.handleClick(props.category.id)}>
        {props.category.mainCategory}
      </li>
      {subCategories}
    </ul>
  );
}

export default MobileCategories;
