import React, { Component } from "react";
import slideData from "./slideData";

class Slider extends Component {
  constructor() {
    super();
    this.state = {
      data: slideData[0]
    };
    this.handleLeft = this.handleLeft.bind(this);
    this.handleRight = this.handleRight.bind(this);
  }

  handleLeft() {
    this.setState(prevState => {
      switch (prevState.data) {
        case slideData[0]:
          return { data: slideData[2] };
        case slideData[1]:
          return { data: slideData[0] };
        case slideData[2]:
          return { data: slideData[1] };
        default:
          return null;
      }
    });
  }

  handleRight() {
    this.setState(prevState => {
      switch (prevState.data) {
        case slideData[2]:
          return { data: slideData[0] };
        case slideData[1]:
          return { data: slideData[2] };
        case slideData[0]:
          return { data: slideData[1] };
        default:
          return null;
      }
    });
  }

  render() {
    const horizontalDivider = {
      height: "2px",
      width: "125px",
      background: "white",
      margin: "10px 0"
    };

    return (
      <div
        className="banner"
        style={{ backgroundImage: "url(" + this.state.data.url + ")" }}
      >
        <div className="sliderArrow leftArrow" onClick={this.handleLeft}>
          <i className="fas fa-chevron-left"></i>
        </div>
        <div className="call-to-action">
          <h1>{this.state.data.title}</h1>
          <div style={horizontalDivider}></div>
          <p>Shop Now</p>
        </div>
        <div className="sliderArrow rightArrow" onClick={this.handleRight}>
          <i className="fas fa-chevron-right"></i>
        </div>
      </div>
    );
  }
}

export default Slider;
