import React from "react";

function MegaMenu(props) {
  const menuStyle = props.category.active ? null : { display: "none" };

  const subCategories = props.category.subCategories.map((subCategory, index) => (
    <li key={index}>{subCategory}</li>
  ));

  const featuredProducts = props.category.featuredProducts.map(
    (featuredProduct, index) => <li key={index}>{featuredProduct}</li>
  );

  const overlay = props.category.active
    ? {
        height: "80%",
        width: "100%",
        position: "absolute",
        background: "rgba(0, 0, 0, 0.5)",
        overflow: "hidden"
      }
    : null;

  const divider = {
    width: "1px",
    height: "285px",
    background: "#808285",
    margin: "auto 20px"
  };

  return (
    <div>
      <div className="mega-menu" style={menuStyle}>
        <div className="menu">
          <h1>Shop By Category</h1>
          <ul>
            <li>Shop All {props.category.mainCategory}</li>
            {subCategories}
          </ul>
        </div>
        <div style={divider}></div>
        <div className="menu">
          <h1>Featured Products</h1>
          <ul>{featuredProducts}</ul>
        </div>
        <div style={divider}></div>
        <div className="menu">
          <img
            src={props.category.primaryImage}
            alt={props.category.primaryLabel}
          />
          <p>Shop {props.category.primaryLabel}</p>
        </div>
        <div style={divider}></div>
        <div className="menu">
          <img
            src={props.category.secondaryImage}
            alt={props.category.secondaryLabel}
          />
          <p>Shop {props.category.secondaryLabel}</p>
        </div>
      </div>
      <div style={overlay}></div>
    </div>
  );
}

export default MegaMenu;
