import React, { Component } from "react";
import Promo from "./Promo";
import Navbar from "./Navbar";
import MegaMenu from "./MegaMenu";
import categoryData from "./categoryData";
import Logo from "./images/logo.svg";
import { Link } from "react-router-dom";

class Header extends Component {
  constructor() {
    super();
    this.state = {
      categories: categoryData
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(id) {
    this.setState(prevState => {
      const updatedCategories = prevState.categories.map(category => {
        if (category.id === id) {
          category.active = !category.active;
        } else category.active = false;
        return category;
      });
      return {
        categories: updatedCategories
      };
    });
  }

  render() {
    const logo = <img className="logo" src={Logo} alt="TableLinensforLess" />;

    const accountText = this.props.header.loggedIn ? (
      <p onClick={this.props.handleLogOut}>
        Hi {this.props.header.firstName},<br />
        <span>Log Out</span>
      </p>
    ) : (
      <Link to="/tablelinensforless/account" style={{ color: "#808285" }}>
        <p onClick={this.props.handleLogOut}>
          Sign Up
          <br />
          Log In
        </p>
      </Link>
    );

    const navigationBar = this.state.categories.map(category => (
      <Navbar
        key={category.id}
        category={category}
        handleClick={this.handleClick}
      />
    ));

    const megaMenus = this.state.categories.map(category => (
      <MegaMenu
        key={category.id}
        category={category}
        active={category.active}
      />
    ));

    return (
      <header>
        <Promo />
        <div className="top-line">
          <div onClick={this.props.handleMobileNav} className="bar-nav">
            <i className="fas fa-bars"></i>
          </div>
          <Link to="/tablelinensforless/">{logo}</Link>
          <div className="search-bar">
            <input type="text" placeholder="Search" />
            <button>
              <i className="fas fa-search"></i>
            </button>
          </div>
          <div className="user-info">
            <div className="account">
              <i className="fas fa-user-circle"></i>
              {accountText}
            </div>
            <div className="bag">
              <Link to="/tablelinensforless/bag">
                <i className="fas fa-shopping-bag"></i>
              </Link>
            </div>
          </div>
        </div>
        <nav>
          {navigationBar}
          <div className="search-bar-mobile">
            <input type="text" placeholder="Search" />
            <button>
              <i className="fas fa-search"></i>
            </button>
          </div>
        </nav>
        {megaMenus}
      </header>
    );
  }
}

export default Header;
