import React, { Component } from "react";
import categoryData from "./categoryData";
import MobileCategories from "./MobileCategories";

class MobileNav extends Component {
  constructor() {
    super();
    this.state = {
      categories: categoryData
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(id) {
    this.setState(prevState => {
      const updatedCategories = prevState.categories.map(category => {
        if (category.id === id) {
          category.active = !category.active;
        } else category.active = false;
        return category;
      });
      return {
        categories: updatedCategories
      };
    });
  }

  render() {
    const mobileCategories = this.state.categories.map(category => (
      <MobileCategories
        key={category.id}
        category={category}
        handleClick={this.handleClick}
      />
    ));

    return (
      <div className="overlay">
        <div className="mobile-nav">
          <ul>{mobileCategories}</ul>
        </div>
        <button className="close" onClick={this.props.handleMobileNav}>
          &times;
        </button>
      </div>
    );
  }
}

export default MobileNav;
