import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Header from "./components/Header";
import MainContent from "./components/MainContent";
import Account from "./components/Account";
import Bag from "./components/Bag";
import Footer from "./components/Footer";
import MobileNav from "./components/MobileNav";
import categoryData from "./components/categoryData";

class App extends Component {
  constructor() {
    super();
    this.state = {
      loggedIn: false,
      firstName: "",
      lastName: "",
      mobileNav: false,
      data: categoryData
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleLogIn = this.handleLogIn.bind(this);
    this.handleLogOut = this.handleLogOut.bind(this);
    this.handleMobileNav = this.handleMobileNav.bind(this);
  }

  handleChange(event) {
    this.setState({
      [event.target.name]: event.target.value
    });
  }

  handleLogIn(event) {
    if (this.state.firstName === "") {
      event.stopPropagation();
    } else {
      this.setState({
        loggedIn: true
      });
    }
  }

  handleLogOut() {
    this.setState({
      loggedIn: false,
      firstName: "",
      lastName: ""
    });
  }

  handleMobileNav() {
    this.setState(prevState => {
      const newData = prevState.data.map(category => {
        if ((category.active = true)) {
          category.active = false;
        }
        return category;
      });
      return {
        data: newData,
        mobileNav: !prevState.mobileNav
      };
    });
  }

  render() {
    const mobileNav = this.state.mobileNav ? (
      <MobileNav handleMobileNav={this.handleMobileNav} />
    ) : null;

    return (
      <Router>
        <div className="app">
          {mobileNav}
          <Header
            header={this.state}
            handleLogOut={this.handleLogOut}
            handleMobileNav={this.handleMobileNav}
          />
          <div className="content-wrapper">
            <Switch>
              <Route exact path="/tablelinensforless/">
                <MainContent />
              </Route>
              <Route path="/tablelinensforless/account">
                <Account
                  account={this.state}
                  handleChange={this.handleChange}
                  handleLogIn={this.handleLogIn}
                />
              </Route>
              <Route path="/tablelinensforless/bag">
                <Bag />
              </Route>
            </Switch>
          </div>
          <Footer />
        </div>
      </Router>
    );
  }
}

export default App;
